import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { m1f } from '../hooks'

const Portrait = ({ imageSrc, alt, effect }) => {
  useEffect(() => {
    m1f.dataImage()
  }, [])
  return (
    <div
      className="avatar relative min-h-[300px] min-w-[300px] rounded-full"
      data-type={effect}
    >
      {' '}
      <div
        className="image absolute inset-0 bg-cover bg-center bg-no-repeat"
        data-img-url={imageSrc}
        data-img-alt={alt}
      />
    </div>
  )
}

Portrait.propTypes = {
  imageSrc: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
  effect: PropTypes.string
}

Portrait.defaultProps = {
  effect: 'wave'
}

export default Portrait
