export const m1f = {
  dataImage() {
    const d = document.querySelectorAll('[data-img-url]')
    for (let i = 0; i < d.length; i++) {
      const element = d[i]
      element.style.backgroundImage = `url(${element.getAttribute(
        'data-img-url'
      )})`
    }
  },
  preloader() {
    const isMobile = !!/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(
      navigator.userAgent
    )
    const preloader = document.getElementById('preloader')

    if (preloader) {
      if (!isMobile) {
        setTimeout(function () {
          preloader.classList.add('preloaded')
        }, 800)
        setTimeout(function () {
          preloader.remove()
        }, 2000)
      } else {
        preloader.remove()
      }
    }
  },
  portfolioHover() {
    const tmPortfolioAnimationWrap = document.querySelectorAll(
      '.tokyo_tm_portfolio_animation_wrap'
    )
    const tmPortfolioTitles = document.querySelector(
      '.tokyo_tm_portfolio_titles'
    )
    tmPortfolioAnimationWrap.forEach((element) => {
      element.addEventListener('mousemove', () => {
        const title = element.getAttribute('data-title')
        const category = element.getAttribute('data-category')
        if (title) {
          tmPortfolioTitles.classList.add('visible')
          tmPortfolioTitles.innerHTML =
            title + '<span class="work__cat">' + category + '</span>'
        }
        document.addEventListener('mousemove', (e) => {
          tmPortfolioTitles.style.left = `${e.clientX - 10}px`
          tmPortfolioTitles.style.top = `${e.clientY + 25}px`
        })
      })
      element.addEventListener('mouseleave', () => {
        tmPortfolioTitles.classList.remove('visible')
      })
    })
  }
}
