import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { getImage, getSrc } from 'gatsby-plugin-image'
import SectionContainer from '../../components/SectionContainer'
import SocialLinksList from '../../components/SocialLinksList'
import Portrait from '../../components/Portrait'
import socialIcons from '../../SocialIcons'

const Preview = () => {
  const { authorJson, aboutMeImage } = useStaticQuery(graphql`
    query {
      authorJson {
        social {
          email
          github
          linkedin
          upwork
          instagram
        }
        roles
        displayName
      }
      aboutMeImage: allFile(filter: { relativePath: { eq: "aboutme.jpg" } }) {
        edges {
          node {
            name
            relativeDirectory
            relativePath
            childImageSharp {
              gatsbyImageData(width: 500)
            }
          }
        }
      }
    }
  `)
  const aboutMeImageSrc = getSrc(
    getImage(aboutMeImage.edges[0].node.childImageSharp.gatsbyImageData)
  )
  const socialLinkItems = []
  Object.entries(authorJson.social).forEach(([key, value]) => {
    if (socialIcons[key]) {
      socialLinkItems.push({
        id: key,
        url: value,
        iconComponent: socialIcons[key]
      })
    }
  })
  return (
    <SectionContainer name={''}>
      <div className="container">
        <div className="tokyo_tm_home relative clear-both flex min-h-[100vh] w-full items-center justify-center">
          <div className="home_content flex items-center">
            <Portrait imageSrc={aboutMeImageSrc} alt={authorJson.displayName} />
            <div className="details ml-[80px]">
              <h3 className="name mb-[14px] text-[55px] font-extrabold uppercase">
                {authorJson.displayName}
              </h3>
              <p className="job font-montserrat mb-[25px] max-w-[450px] font-medium">
                {authorJson.roles.join('. ')}
              </p>
              <SocialLinksList items={socialLinkItems} />
            </div>
          </div>
        </div>
      </div>
    </SectionContainer>
  )
}
export default Preview
